import { useRef, useState, useEffect } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import styles from './login.module.scss';

interface props {
  login(): void;
}

type pageStateType = 'login' | 'registration' | 'recovery' | 'sentMail' | 'loading';

function Login({ login }: props) {
  const [pageState, setPageState] = useState<pageStateType>('login');
  const [error, setError] = useState('');
  const formRef = useRef<any>(null);

  useEffect(() => {
    setError('');
  }, [pageState]);

  const sendLogin = () => {
    if (error !== '') {
      return;
    }
    if (formRef.current.login?.value && formRef.current.pass?.value) {
      login();
    } else {
      setError('Заполните все поля');
    }
  };

  const sendRegistration = () => {
    if (error !== '') {
      return;
    }
    if (
      formRef.current.login?.value &&
      formRef.current.mail?.value &&
      formRef.current.pass?.value &&
      formRef.current.repeatPass?.value
    ) {
      setError('Такой пользователь уже существует');
    } else {
      setError('Заполните все поля');
    }
  };

  const sendRecovery = () => {
    if (error !== '') {
      return;
    }
    if (formRef.current.login?.value) {
      setPageState('sentMail');
    } else {
      setError('Введите свой логин');
    }
  };

  const validateForm = () => {
    if (formRef.current?.login.value) {
      const loginValue: string = formRef.current.login.value;

      if (loginValue.length < 6) {
        setError('Логин не может быть меньше 6 символов');
        return;
      }
      if (loginValue.length > 18) {
        setError('Логин не может быть больше 18 символов');
        return;
      }
    }

    if (formRef.current.mail?.value) {
      const mailValue: string = formRef.current.mail.value;
      const mailRE = /\S+@\S+\.\S+/;
      if (!mailRE.test(mailValue)) {
        setError('Введите корректную почту');
        return;
      }
    }

    if (formRef.current.pass?.value) {
      const passValue: string = formRef.current.pass.value;
      if (passValue.length < 6) {
        setError('Пароль не может быть меньше 6 символов');
        return;
      }
      if (passValue.length > 18) {
        setError('Пароль не может быть больше 18 символов');
        return;
      }
    }

    if (formRef.current.repeatPass?.value) {
      const repeatPassValue: string = formRef.current.repeatPass.value;
      if (repeatPassValue !== formRef.current.pass.value) {
        setError('Пароли должны совпадать');
        return;
      }
    }

    setError('');
  };

  return (
    <div className={styles.container}>
      {pageState === 'login' ? (
        <>
          <div className={styles.title}>Вход</div>
          <form ref={formRef} className={styles.form} onBlur={validateForm}>
            <TextField
              name='login'
              style={{ marginBottom: 10 }}
              label='Логин'
              error={Boolean(error)}
            />
            <TextField
              name='pass'
              style={{ marginBottom: 10 }}
              label='Пароль'
              type='password'
              error={Boolean(error)}
            />
            <div style={{ height: 58, width: '100%' }}></div>
            <div style={{ height: 58, width: '100%' }}></div>
          </form>
          <Button
            style={{ fontSize: 18, margin: '20px 0' }}
            variant='contained'
            onClick={sendLogin}
          >
            Отправить
          </Button>
          <div className={styles.row}>
            <Button
              style={{ fontSize: 12 }}
              onClick={() => setPageState('registration')}
            >
              Зарегистрироватся
            </Button>
            <Button style={{ fontSize: 12 }} onClick={() => setPageState('recovery')}>
              Забыли пароль?
            </Button>
          </div>
          {error ? <div className={styles.error}>{error}</div> : null}
        </>
      ) : null}
      {pageState === 'registration' ? (
        <>
          <div className={styles.title}>Регистрация</div>
          <form ref={formRef} className={styles.form} onBlur={validateForm}>
            <TextField
              name='login'
              style={{ marginBottom: 10 }}
              label='Логин'
              error={Boolean(error)}
            />
            <TextField
              name='mail'
              style={{ marginBottom: 10 }}
              label='Почта'
              error={Boolean(error)}
            />
            <TextField
              name='pass'
              style={{ marginBottom: 10 }}
              label='Пароль'
              type='password'
              error={Boolean(error)}
            />
            <TextField
              name='repeatPass'
              style={{ marginBottom: 10 }}
              label='Повторите пароль'
              type='password'
              error={Boolean(error)}
            />
          </form>
          <Button
            style={{ fontSize: 18, margin: '20px 0' }}
            variant='contained'
            onClick={sendRegistration}
          >
            Отправить
          </Button>
          <div className={styles.row}>
            <Button style={{ fontSize: 12 }} onClick={() => setPageState('login')}>
              Войти
            </Button>
          </div>
          {error ? <div className={styles.error}>{error}</div> : null}
        </>
      ) : null}
      {pageState === 'recovery' ? (
        <>
          <div className={styles.title}>Восстановить пароль</div>
          <form ref={formRef} className={styles.form} onBlur={validateForm}>
            <TextField
              name='login'
              style={{ marginBottom: 10 }}
              label='Логин'
              error={Boolean(error)}
            />
            <div style={{ height: 58, width: '100%' }}></div>
            <div style={{ height: 58, width: '100%' }}></div>
            <div style={{ height: 58, width: '100%' }}></div>
          </form>
          <Button
            style={{ fontSize: 18, margin: '20px 0' }}
            variant='contained'
            onClick={sendRecovery}
          >
            Отправить
          </Button>
          <div className={styles.row}>
            <Button style={{ fontSize: 12 }} onClick={() => setPageState('login')}>
              Войти
            </Button>
            <Button
              style={{ fontSize: 12 }}
              onClick={() => setPageState('registration')}
            >
              Регистрация
            </Button>
          </div>
          {error ? <div className={styles.error}>{error}</div> : null}
        </>
      ) : null}
      {pageState === 'sentMail' ? (
        <>
          <div className={styles.title}>
            На вашу почту было отправлено письмо с паролем
          </div>
          <Button
            style={{ fontSize: 18, margin: '20px 0' }}
            variant='contained'
            onClick={() => setPageState('login')}
          >
            Войти
          </Button>
        </>
      ) : null}
    </div>
  );
}

export default Login;
